<template>
    <div class='promotion'>
        <div class='top' v-if='show'>
            <span>运营推广</span>
        </div>
        <div class='top' v-if='!show'>
            <div class='parTop'>
                <span @click='goback()' class='back'>运营推广</span>
                <img src='../../assets/images/icon_nav_arrow.png'/>
                <span class='branch'>免费体验券</span>
            </div>
            <div class='text'>
                <span><i class="iconfont icon-tishi"></i>未预约过某类课程的用户在活动时间内，可自动获得系统发送的免费体验券预约该种类课程</span>
                <div @click='addPlan'><i class="iconfont icon-btn_icon_add"></i>添加活动</div>
            </div>
        </div>
        <div class='free' @click="freeList" v-if='show'>
            <img src='../../assets/images/pic_free.png'/>
            <div class='text'>
                <div class='fir'>免费体验礼</div>
                <div class='sec'>新用户免费体验，促进转化</div>
            </div>
        </div>
        <Table  :tableData="tableData" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata" v-if='!show'></Table>
    </div>
</template>
<script>
import Table from '../../components/Table'
import {freeCardList, updateFreeStatue, freeDetail} from '@/services/service'
import {isApiResponseOk, getSessionStorage, setSessionStorage} from '@/utils/com'
export default {
    data () {
        return {
            tableKey: [
                {name: '体验券名称', value: 'couponName', nor: true},
                {name: '赠送课程种类', value: 'coursePOList', freeList: true},
                {name: '赠送张数', value: 'couponNumber', nor: true},
                {name: '有效期至', value: 'couponEndTime', nor: true},
                {name: '赠券开始时间', value: 'activityStartTime', nor: true},
                {name: '赠券结束时间', value: 'activityEndTime', nor: true},
                {name: '启用状态', value: 'status', nor: true},
                {name: '活动创建时间', value: 'createTime', nor: true},
                {name: '创建人', value: 'creatorName', nor: true},
                {name: '操作', value: 'btnGroup', btn: true}
            ],
            tableData: [],
            isPage: true,
            total: 0,
            tabhide: true,
            pageSize: 10,
            para: {pageNo: 1, pageSize: 10},
            show: true // 控制优惠券显示隐藏的
        }
    },
    methods: {
        freeList () {
            this.$router.push({name: 'promotion', query:{showList: true}})
        },
        getRowdata (data) {
            console.log('数据', data)
            if (data.index === '启用' || data.index === '禁用') {
                // 启用禁用
                this.updateStatus(data.row)
            } else if (data.index === '详情') {
                this.freeDetail(data.row)
            } else if (data.index === '领取详情') {
                this.$router.push({name: 'cardDetail', query:{code: data.row.code}})
            }
        },
        changeParam (data) {
            this.para.pageNo = data
            this.initList()
        },
        async updateStatus (data) {
            var status = ''
            if (parseInt(data.status) === 1) {
                status = 0
            } else if (parseInt(data.status) === 0) {
                status = 1
            }
            const res = await updateFreeStatue(data.code, status)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '修改成功',
                    type: 'success'
                })
                this.initList()
            }
        },
        async freeDetail (data) {
            const res = await freeDetail(data.code)
            if (isApiResponseOk(res)) {
                setSessionStorage('freeDetail', res.data)
                this.$router.push({name: 'addpromotion', query: {modify: true}})
            }
        },
        // 添加活动
        addPlan () {
            this.$router.push({name: 'addpromotion'})
        },
        // 获取优惠券列表
        async initList () {
            const res = await freeCardList (this.para)
            if (isApiResponseOk(res)) {
                this.tableData = res.data.record
                this.total = res.data.totalNum
                for (let val of res.data.record) {
                    if (val.status != '1') {
                        val['btnGroup'] = [{text: '禁用', style: 'color: #F44A6C;'}, {text: '详情'}, {text: '领取详情'}]
                    } else {
                        val['btnGroup'] = [{text: '启用'}, {text: '详情'}, {text: '领取详情'}]
                    }
                }  
            }
        },
        goback () {
            this.$router.push({name: 'promotion'})
        }
    },
    created () {
        this.show = (!this.$route.query.showList) ? true : false
        if (!this.show) {
            this.initList()
        }
        console.log('ddd', this.show)
    },
    watch: {
        $route(to,from){
            this.show = (!to.query.showList) ? true : false
            if (!this.show) {
                this.initList()
            }
        }
    },
    components: {
        Table
    }
}
</script>
<style lang="scss" scoped>
.promotion{
  .top{
    font-family: PingFangSC-Semibold;
    font-size: .21rem;
    color: #263238;
    letter-spacing: 0;
    line-height: .21rem;
    margin-bottom: .3rem;
    display: flex;
    .parTop{
        font-family: PingFangSC-Semibold;
        font-size: .17rem;
        color: #8597A1;
        letter-spacing: 0;
        line-height: .17rem;
        flex: 1;
        .branch{
            font-size: .21rem;
            color: #263238;
            letter-spacing: 0;
            line-height: .21rem;
        }
        img{
            margin-left: .12rem;
            margin-right: .2rem;
        }
        .back{
            cursor: pointer;
        }
    }
    .text{
        font-family: PingFangSC-Medium;
        font-size: .11rem;
        color: #8597A1;
        letter-spacing: 0;
        line-height: .11rem;
        align-items: center;
        display: flex;
        div{
            border: 1px solid #20C6BA;
            border-radius: 2px;
            color: #20C6BA;
            width: 1.29rem;
            height: .36rem;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: .3rem;
            cursor: pointer;
        }
        .icon-btn_icon_add:before{
            margin-right: .1rem;
        }
        .icon-tishi:before{
            font-size: 12px;
            margin-right: 9px;
        }
    }
  } 
  .free{
    width: 2.66rem;
    cursor: pointer;
    img{
        vertical-align: middle;
        width: 2.66rem;
        height: 1.6rem;
    }
    .text{
        background: #fff;
        padding: .21rem .24rem;
        width: 2.66rem;
        .fir{
            font-family: PingFangSC-Medium;
            font-size: .16rem;
            color: rgba(0,0,0,0.85);
            line-height: .24rem;
        }
        .sec{
            font-family: PingFangSC-Regular;
            font-size: .14rem;
            color: rgba(0,0,0,0.45);
            line-height: .22rem;
            margin-top: .04rem;
            margin-bottom: .11rem;
        }
        .thi{
            font-family: HelveticaNeue;
            font-size: .12rem;
            color: rgba(0,0,0,0.45);
            line-height: .2rem;
        }
    }
  }
}
</style>





